@import "/src/assets/styles/colors";

.mailbox-wrapper {
  display: flex;
  flex-direction: row;
  max-width: 1280px;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  overflow: hidden;
  height: calc(100vh - 6.4rem);
  position: relative;
}

.mailbox-wrapper > * {
  mix-blend-mode: multiply;
}

.mailbox-side {
  max-width: 15rem;
  display: flex;
  flex-direction: column;
  flex: 1;
  background-color: #eeeeee;
  border-right: solid 0.5px gray;
  h3 {
    border-bottom: solid 0.5px gray;
    padding-bottom: 1rem;
    margin-bottom: 0;
  }
}

.mailbox-side > * {
  padding-left: 1rem;
}
.mailbox-side > p {
  margin-top: 0;
  margin-bottom: 0;
  background-color: white;
  padding: 0.5rem;
}

.mailbox-side > p:hover {
  cursor: pointer;
}

.mailbox-side > p:hover {
  background-color: $grayish-fg;
}

.mailbox-main {
  background-color: white;
  flex: 1;
  display: flex;
  flex-direction: column;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
  height: 100%;
  max-height: 50%;
  overflow: scroll;
  overflow-x: hidden;
  display: block;
  user-select: none;
  border-bottom: 1px solid #dddddd;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 0.5rem;
}

td {
  border: unset;
}

tbody tr:hover {
  background-color: $grayish-fg !important;
  cursor: pointer;
}

td:nth-child(1) {
  width: 10rem;
}

td:nth-child(2) {
  width: 100%;
}

td:nth-child(3) {
  width: 10rem;
}

td {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

tr:nth-child(even) {
  background-color: #dddddd;
}
tr:nth-child(odd) {
  background-color: white;
}

.mail-selected {
  background-color: $grayish-bg !important;
}

.reply-cell:hover {
  cursor: pointer !important;
  color: $dark-fg !important;
}

.mailbox-main-content {
  width: 100%;
  flex: 1;
  padding: 0.5rem;
  background-color: white;
}

.message-content-topic {
  margin-top: 0;
  margin-bottom: 0;
}
.message-content-user {
  color: #505050;
  width: 100%;
  font-size: small;
  display: block;
  margin-bottom: 1rem;
}
.message-content-content {
  padding: 0.3rem;
  white-space: pre-wrap;
  //to keep padding
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
}
.message-content-footer {
  margin-top: 1rem;
  border-top: solid 1px gray;
  color: #505050;
  width: 30%;
  min-width: max-content;
  display: block;
}

.sendmail-btn {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: x-large;
  width: 100%;
  border: unset;
  background-color: rgba(saddlebrown, 0.7);
  transition: all 0.2s ease-in-out;
  cursor: pointer !important;
  user-select: none;
  border-bottom: 1px solid black;
  p {
    margin: 0.4rem;
  }
}

.sendmail-btn:hover {
  background-color: rgba(saddlebrown, 0.5);
}
