$mult: 0.8;

.mercenary-wrapper {
  //keep a 13:15 ratio
  height: calc($mult * 15rem);
  width: calc($mult * 13rem);
  padding: calc($mult * 15rem / 13);
  display: flex;
  flex-direction: column;
  cursor: pointer;
  user-select: none;
}
.mercenary-wrapper::after {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: url("../../assets/images/old-paper.png") center center fixed
    no-repeat;
  background-size: cover;
  //scaling!
  transform: scale(1.2);
}

.mercenary-wrapper:hover {
  transform: scale(1.1);
}

.mercenary-image-wrapper {
  overflow: hidden;
  flex: 1;
  order: 0;
  margin: 5px;
  //old paper color to somewhat mix the image
  border: 5px solid #c7a575;
}

.mercenary-image-wrapper > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.mercenary-name {
  text-align: center;
  margin: 0;
  flex-grow: 0;
}

.mercenary-misc {
  margin: 0;
  flex-grow: 0;
  font-size: small;
  padding: 0 1rem;
}

a.customnostyle,
.mercenary-wrapper {
  transition: all 0.2s ease-out;
}

a.customnostyle:link {
  display: inline-block;
  width: calc($mult * 13rem * 1.2);
  height: calc($mult * 15rem * 1.2);
}
a.customnostyle:link:hover {
  transform: rotate(0deg) scale(1.4);
  z-index: 11;
  .mercenary-wrapper {
    transform: rotate(4deg) !important;
  }
}

a.customnostyle:link:active {
  z-index: 100;
  transform: scale(1.3);
}

a.customnostyle:visited {
  display: inline-block;
  width: calc($mult * 13rem * 1.2);
}
